* {
  padding: 0;
  margin: 0;
  box-sizing: inherit;
}

body {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.heading-primary {
  font-size: 3em;
  color: #84eaa7;
  text-align: center;
  padding-top: 150px;
  padding-bottom: 20px;
}

.heading-secondary {
  font-size: 2em;
  color: white;
  text-align: center;
  padding: 100px 0;
}

.description-text {
  color: white;
  font-size: 1.5rem;
  text-align: left;
}

.u-margin-btm-lg {
  margin-bottom: 100px;
}

.u-margin-btm-md {
  margin-bottom: 50px;
}

.App {
  display: block;
  min-height: 100vh;
}

.HeaderContainer {
  flex-grow: 1;
  background-color: #1d1e24;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 80px;
  z-index: 3;
}

.content {
  font-size: 15px;
  color: #ffffff;
  font-family: "Roboto", sans-serif;
}
a:visited {
  color: white;
}

a:active {
  color: #84eaa7;
}

.name {
  font-size: 15px;
  font-weight: bold;
  color: #ffffff;
  font-family: "Roboto", sans-serif;
}

.Home {
  display: flex;
  background-color: #33363d;
  padding-top: 80px;
  min-height: 100vh;
  justify-content: center;
  align-items: center;
}

.Home__container {
  width: 40%;
  /* background-color: blue; */
  align-items: center;
  justify-content: center;
  font-size: 15px;
  color: #ffffff;
  text-align: center;
  justify-content: space-around;
}

.Home__buttons {
  display: flex;
  width: 100%;
  justify-content: space-evenly;
}

h1 {
  font-size: 3em;
  color: #84eaa7;
}

.button {
  background-color: transparent;
  border: 1px solid #84eaa7;
  border-radius: 5px;
  color: #84eaa7;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  outline: none;
  bottom: 0;
}

.Projects {
  background-color: #33363d;
  min-height: 100vh;
}

.button {
  background-color: transparent;
  border: 1px solid #84eaa7;
  border-radius: 5px;
  color: #84eaa7;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  outline: none;
  bottom: 0;
}

.game-container {
  display: flex;
  justify-content: center;
}

.carousel-container {
  display: flex;
  justify-content: center;
  padding-top: 200;
}

.desc-container {
  width: 100%;
  display: flex;
  justify-content: center;
  padding-left: 20%;
  padding-right: 20%;
}


.Experience {
  background-color: #33363d;
  min-height: 100vh;
}

.Experience__container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 15px;
  width: 60%;
  margin: 0 auto;
}

h1 {
  font-size: 2em;
  color: white;
}

.stackContainer {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  border-bottom: 1px solid white;
  border-top: 1px solid white;
}

.stackItem {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 50px;
  width: 200px;
  border-radius: 10px;
  background-color: white;
  color: black;
  margin: 10px;
}

p {
  padding-left: 15px;
}

.Education {
  background-color: #33363d;
  min-height: 100vh;
}

.Education__container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 15px;
  width: 60%;
  margin: 0 auto;
}

.heading-secondary-education {
  font-size: 2em;
  color: white;
  text-align: center;
  padding: 50px 0;
}

.Contact {
  background-color: #33363d;
  min-height: 100vh;
}

.Contact__container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 15px;
  width: 60%;
  margin: 0 auto;
}

.profileContainer {
  padding-top: 50px;
  display: flex;
  width: 100%;
  justify-content: center;
}

.profile {
  border-radius: 50%;
  width: 300px;
  height: 300px;
}

.contactFormContainer {
  display: flex;
  justify-content: center;
}

.contactForm {
  display: flex;
  flex-direction: column;
  color: white;
}

.contactFormField {
  color: white;
  font-size: 1.5rem;
  text-align: left;
  padding-top: 20px;
}

.icon {
  margin-right: 30px;
  color: white;
}

